import React, { Component } from 'react'


export default class Add extends Component{
    constructor(props){
        super(props)

    }




    render(){
        <div>
            <h1>添加博客</h1>
            <h3>标题</h3>
            <input type="text"/>
            <h3>内容</h3>
            <textarea /> 
            <input type="button" onClick={saveArticle}/>
        </div>
    }
}